import React, {useEffect, useState} from 'react'
import dayjs from 'dayjs'
import {Card, DateRangePicker, Pagination, Select, Table} from "element-react";
import {HiDownload} from "react-icons/hi";

import BarLineChart from '../components/BarLineChart';
import DoughnutChart from "../components/DoughnutChart";
import {
    getCustomBillPie,
    getCustomFilters,
    getCustomTable, getCustomTableDownload,
    getCustomValueBillBarLine,
    getCustomValuePie
} from "../apis/custom";
import {SearchCustomParams} from "../types/custom";
import {formatDate} from "../utils/common";
import {TableResponse} from "../types/custom";
import {PieChartItem} from "../types/chart";
import {BarLineChartData} from "../types/market";
import {getProductsDownloadUrl} from "../apis/product";

function CustomsDataDashBoard() {
    const [exporter, setExporter] = useState('');
    const [code, setCode] = useState('');
    const [date, setDate] = useState<ElementReactLibs.dateType[]>([dayjs('2018-01-01').toDate(), dayjs('2021-06-30').toDate()]);

    const [exporterOptions, setExporterOptions] = useState<ElementReact.SelectOptionProps[]>([])
    const [codeOptions, setCodeOptions] = useState<ElementReact.SelectOptionProps[]>([])

    const [page, setPage] = useState(1)

    const [valuePie, setValuePie] = useState<PieChartItem[]>([])
    const [billPie, setBillPie] = useState<PieChartItem[]>([])
    const [barLine, setBarLine] = useState<BarLineChartData | null>(null)
    const [table, setTable] = useState<TableResponse | null>(null)

    const columns: ElementReact.TableColumn[] = [
        {label: 'Exporter', prop: 'Exporter'},
        {label: 'HS Code', prop: 'HS Code'},
        {label: 'Importer', prop: 'Importer'},
        {label: 'Destination Country', prop: 'Destination Country', width: 180},
        {label: 'Value', prop: 'Value'},
        {label: 'Quantity', prop: 'Quantity'},
        {label: 'Quantity Unit', prop: 'Quantity Unit'},
        {label: 'Unit Price', prop: 'Unit Price'},
    ]

    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getCustomFilters()
            setExporterOptions(data.exporters.map((value: string): ElementReact.SelectOptionProps => ({
                label: value,
                value
            })))
            setCodeOptions(data.hs_codes.map((value: string): ElementReact.SelectOptionProps => ({
                label: value,
                value
            })))
        }
        fetchData()
    }, [])

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date);
            const params: SearchCustomParams = {
                exporter,
                hs_code: code,
                start_at,
                end_at,
                page,
            }
            setTable((await getCustomTable(params)).data)
        }
        fetchData()
    }, [exporter, code, date, page])

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date);
            const params: SearchCustomParams = {
                exporter,
                hs_code: code,
                start_at,
                end_at,
            }
            const {data: value} = await getCustomValuePie(params)
            const {data: bill} = await getCustomBillPie(params)
            setBarLine((await getCustomValueBillBarLine(params)).data)
            setValuePie(value.series.data)
            setBillPie(bill.series.data)

        }
        fetchData()
    }, [exporter, code, date])

    const handleDownloadTable = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchCustomParams = {
            exporter,
            hs_code: code,
            start_at,
            end_at
        }
        const {request} = await getCustomTableDownload(params)
        window.open(request.responseURL, '__blank')
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-end mb-4">
                <Select className="mr-4" value={exporter} placeholder="Select" onChange={(value: string) => setExporter(value)}>
                    {
                        exporterOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={code} placeholder="Select" onChange={(value: string) => setCode(value)}>
                    {
                        codeOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
            </div>

            {/* Dashboard Charts */}
            <Card className="mb-12">
                <div className="flex">
                    <div className="w-2/3">
                        <BarLineChart title="Weight, Import Value and Bill of Lading" height="600px" data={barLine}/>
                    </div>
                    <div className="w-1/3">
                        <DoughnutChart title="Top Importer by Import Value" height="300px" data={valuePie}/>
                        <DoughnutChart title="Top Importer by Bill of Lading" height="300px" data={billPie}/>
                    </div>
                </div>
            </Card>

            {/* Dashboard Table */}
            <Card className="relative mb-12 pt-8">
                <HiDownload className="absolute right-4 top-4 text-3xl cursor-pointer z-30" onClick={handleDownloadTable}/>

                {
                    table &&
                        <>
                          <Table
                            style={{width: '100%'}}
                            columns={columns}
                            data={table.items}
                          />
                          <div className="flex justify-center pt-4">
                            <Pagination
                              layout="prev, pager, next"
                              pageCount={table.info.max_page}
                              currentPage={table.info.current_page}
                              onCurrentChange={(value: number) => setPage(value)}
                            />
                          </div>
                        </>
                }
            </Card>
        </div>
    )
}

